@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
* {
  font-family: "Quicksand", sans-serif;
  font-weight: 400;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  background-color: #252934;
  font-size: 20px;
  color: white;
  font-family: "Quicksand", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  color: white;
  text-decoration: none;
  font-weight: 700;
}

a:link, button, .bars {
  transition: color 0.2s ease-in-out;
}

a:hover {
  color: rgb(80, 230, 240);
}

h1 {
  font-size: calc(20px + 8vmin);
  font-weight: 300;
  margin: 0 0 10px 0;
  padding: 0;
}

h2 {
  margin: 0 0 25px 0;
  padding: 0;
  font-size: calc(20px + 3vmin);
  font-weight: 400;
}

h3 {
  font-size: calc(10px + 2vmin);
  font-weight: 700;
  margin: 0 0 20px 0;
}

hr {
  margin: 0 auto;
  padding: 0;
  height: 1px;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(100, 0, 0, 0.75), rgba(0, 0, 0, 0));
  width: 50vw;
}

section {
  margin: 0 auto 8em;
  width: 90vw;
  max-width: 1200px;
  padding-top: 70px;
}

section > h2:hover::after {
  content: " #";
  color: gray;
}

svg {
  width: calc(10px + 8vw);
  height: calc(10px + 8vw)
}

input, textarea, button {
  padding: 10px;
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: 500;
  border: none;
  border-radius: 10px;
}
input:nth-child(1) {
  width: 25%;
}
input:nth-child(2) {
  width: 50%;
}
textarea {
  height: 1.3em;
  width: 75%;
  max-width: 80vw;
  min-height: 1.3em;
  max-height: 5em;
}
button {
  background-color: gray;
  font-weight: 700;
  color: white;
}
button:hover {
  color: rgb(80, 230, 240);
  cursor: pointer;
}

nav {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(80, 80, 80, 0.95);
  padding: 15px;
  height: 30px;
}
nav .logo {
  font-size: 36px;
  font-weight: 600;
}
nav .links a {
  margin-left: 30px;
}
nav .bars {
  font-size: 36px;
}
nav .bars:hover {
  color: rgb(80, 230, 240);
  cursor: pointer;
}
.mobile-nav {
  position: fixed;
  display: flex;
  flex-direction: column;
  width: 100%;
  text-align: center;
}
.mobile-nav a {
  padding: 10px;
  border-bottom: 1px solid white;
  background-color: rgb(50, 50, 50, 0.95);
  animation: slide-down 0.4s ease;
}

footer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px;
  height: 36px;
  border-top: 1px solid white;
  font-size: 22px;
}

footer a {
  font-size: 40px;
  margin-left: 20px;
}

.inner {
  display: flex;
  flex-wrap: flex;
  justify-content: space-between;
  align-items: center;
  width: 90vw;
  max-width: 1200px;
}

.header {
  background: url(/portfolio/static/media/stars.90c266f7.png) repeat top center;
  min-height: calc(100vh - 30px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}
.sticky {
	position: -webkit-sticky;
	position: sticky;
	top: 0;
  z-index: 1;
}

.about .skills {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 1fr 1fr;
}
.about .skills > li {
  border: 1px solid white;
  border-radius: 10px;
  padding: 10px;
  font-weight: 600;
}

.contact {
  display: flex;
  flex-direction: column;
}
.contact form {
  display: grid;
}

.projects {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 3rem;
}
.projects h3 {
  margin: 0 0 15px 0;
}
.projects img {
  width: 100%;
  border-radius: 10px;
  margin-bottom: 10px;
  box-shadow: 3px 3px 5px 5px #c7c7c7;
  transition: box-shadow 200ms;
}

.projects img:hover {
  box-shadow: 3px 3px 20px 5px #aaa;
}
.projects .project {
  background-color: rgb(231, 235, 238, 0.9);
  border-radius: 10px;
  padding: 20px 25px;
  color: black;
  box-shadow: 3px 3px 8px 8px #111;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.projects .project .icons {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}
.projects .project-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.projects .project-links {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
  font-size: 32px;
}
.projects .project-links a {
  color: black;
  margin-left: 20px;
}
.projects .project-links a:hover {
  color: white;
}
.more-projects {
  margin-top: 50px;
  font-size: 25px;
  text-align: center;
}

.desktop {
  display: none;
}

@media (min-width: 768px) {
  svg {
    width: calc(25px + 2vw);
    height: calc(25px + 2vw)
  }
  .inner, section {
    width: 80vw;
  }
  .mobile {
    display: none;
  }
  .desktop {
    display: block;
  }
  .projects {
    grid-template-columns: repeat(2, 1fr);
  }
  .about .skills {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media screen and (prefers-reduced-motion: reduce) {
  html {
    scroll-behavior: smooth;
  }
}

@keyframes slide-down {
  0% {
      opacity: 0;
      transform: translateY(-20px);
  }
  100% {
      opacity: 1;
      transform: translateY(0);
  }
}
